 class DashboardWidget {
  /*
   * vue
   * @ref: vuerefdom
   * @base_url: 
   * @token: token
   * @_theme: ,
   * @_settings: ,
   * @_mack: mock
   * @_dataset: 
   * @_range_id: id
   * @_date_range: 
   * */
  constructor(ref, base_url, token) {
      this.ref = ref;
      this.base_url = base_url;
      this.token = token;
      this._theme = 'dark';
      this._settings = {};
      this._mack = '';
      this._dataset = [];
      this._range_id = '';
      this._date_range = {};
  }

  /**
   * todo:
   * ID
   */
  getUniqueID() {
      throw new Error("ID");
  }

  /**
   * ID
   * @param {*} range_id
   * @returns
   */
  setRangeId(range_id) {
      this._range_id = range_id;
      return this;
  }

  /**
   * ID
   * @returns
   */
  getRangeId() {
      return this._range_id;
  }

  /**
   * 
   * @param {*} date_range
   * @returns
   */
  setDateRange(date_range) {
      this._date_range = date_range;
      return this;
  }

  /**
   * 
   * @returns
   */
  getDateRange() {
      return this._date_range;
  }

  /**
   * APIresize
   * 
   */
  resetDataset() {
      this._dataset = [];
      return this;
  }

  /**
   * 
   * @returns
   */
  getThemes() {
      return [
          {id: 'dark', name: ''},
          {id: 'light', name: ''},
      ];
  }

  /**
   * 
   * @returns
   */
  getTheme() {
      return this._theme;
  }

  /**
   * 
   * @param {*} theme
   * @returns
   */
  setTheme(theme) {
      // getThemes
      this._theme = theme;
      return this;
  }

  /**
   * 
   * @returns
   */
  getSettings() {
      return this._settings;
  }

  /**
   * 
   * @param {*} settings
   * @returns
   */
  setSettings(settings) {
      this._settings = settings;
      return this;
  }

  /**
   * Mock
   * @returns
   */
  getMock() {
      return this._mack;
  }

  /**
   * Mock
   * MockAPI
   * @returns
   */
  setMock(mack) {
      this._mack = mack;
      return this;
  }
  /**
   * dataset
   * @returns
   */
  getDataset() {
      return this._dataset;
  }

  /**
   * 
   */
  async init () {
      if(this.ref==undefined){
          throw new Error('ref')
      }
      if(this.base_url==undefined){
          throw new Error('base_url')
      }
      if(this.token==undefined){
          throw new Error('token')
      }
      if (this._dataset.length === 0) {
          // dataset
          if (this._mack === '') {
              // API
              this._dataset = await this.load_data();
          } else {
              // MockMock
              this._dataset = eval(this._mack);
          }
      }

      // 
      await this.render();
  }

  /**
   * 
   * render()
   */
  async load_data() {
  }

  /**
   * 
   * render()
   */
  async render() {}

  /**
   * todo:
   * : 
   * 
   * @returns
   */
  getSettingsConfig() {
      return [];
  }

  /**
   * : 
   */
  async preview() {
      this.init();
  }

  /**
   * : 
   */
  async enveope() {
      this.preview();
  }
}

export {DashboardWidget}